/*
  Description: project's variables
*/
:root {
  --light-color: #4bc4b1;
  --main-color: #5f5f5f;
  --secondary-color: #1a4860;
  --site-color: #1B4751;
  --high-contrast-color: #AFDED6;
  --dark-bg: #173d51;
  --break-small: 415px;
  --break-medium: 768px;
  --break-large: 1024px;
  --break-wide: 1280px;
}

.chapo {
  font-weight: bold;
  margin-bottom: 30px;
}

article, .simple_content {
  padding: 60px 20px;
}
article img, .simple_content img {
  display: block;
  max-width: 100%;
  margin: 0 auto;
}
article a, .simple_content a {
  color: #173d51;
}
article a:after, .simple_content a:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 100%;
  background: #173d51;
  transition: width 0.2s;
}
article a:focus, article a:active, article a:hover, .simple_content a:focus, .simple_content a:active, .simple_content a:hover {
  color: var(--main-color);
}
article a:focus:after, article a:active:after, article a:hover:after, .simple_content a:focus:after, .simple_content a:active:after, .simple_content a:hover:after {
  width: 0;
  background: var(--main-color);
}
article a:focus-visible, .simple_content a:focus-visible {
  outline: 2px solid var(--site-color);
}
article ul, .simple_content ul {
  margin: 0 0 2rem 2rem;
}
article h2, article h3, .simple_content h2, .simple_content h3 {
  margin-top: 4rem;
}
article p, article ul li, .simple_content p, .simple_content ul li {
  font-size: 1.8rem;
  line-height: 1.55;
}

article img {
  max-width: 50%;
}

.wrapper + .wrapper {
  padding-top: 0;
}

.post_list > div {
  position: relative;
  display: table;
  width: 100%;
  margin: 0 0 40px 0;
}
.post_list > div > a, .post_list > div > img, .post_list > div .content {
  display: table-cell;
  vertical-align: top;
}
@media (max-width: 63.99em) {
  .post_list > div > a, .post_list > div > img, .post_list > div .content {
    display: block;
  }
}
.post_list > div > a {
  width: 320px;
}
@media (max-width: 63.99em) {
  .post_list > div > a {
    width: 100%;
  }
}
.post_list > div > a:focus img, .post_list > div > a:active img, .post_list > div > a:hover img {
  box-shadow: 0 0 15px #d0d0d0;
  transform: scale(1.02, 1.02);
}
@media (prefers-reduced-motion: reduce) {
  .post_list > div > a:focus img, .post_list > div > a:active img, .post_list > div > a:hover img {
    transform: none;
  }
}
.post_list > div > a:focus + .content h2:after, .post_list > div > a:active + .content h2:after, .post_list > div > a:hover + .content h2:after {
  width: 100%;
}
.post_list > div > a:focus-visible::before {
  outline: 2px solid var(--site-color);
}
.wrapper .post_list > div > a:after {
  width: 0;
}
.post_list > div > a img {
  box-shadow: 0 0 5px #f1f1f1;
  transition: box-shadow 0.2s, transform 0.2s;
}
@media (max-width: 63.99em) {
  .post_list > div > a img {
    width: 100%;
    height: auto;
  }
}
.post_list > div > img {
  width: 320px;
  max-width: none;
}
@media (max-width: 63.99em) {
  .post_list > div > img {
    width: 100%;
  }
}
.post_list .content {
  padding-left: 20px;
}
@media (max-width: 63.99em) {
  .post_list .content {
    padding: 20px 0 0 0;
  }
}
.post_list h2 {
  display: inline-block;
  font-weight: bold;
}
.wrapper .post_list h2 {
  margin: 0 0 10px;
}
.post_list h2:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 0;
  background: #173d51;
  transition: width 0.2s;
}

.sitemap {
  -webkit-perspective: 200px;
