article .highlight {
  position: relative;
  overflow-x: auto;
  background: #f0f0f0;
  padding: 2rem;
  margin-bottom: 2rem;
}

.cols {
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 63.99em) {
  .cols {
    grid-template-columns: 1fr;
  }
}
.cols img {
  width: 100%;
  max-width: none;
}
.cols figcaption {
  font-size: 1.4rem;
}

