.comment {
  padding: 20px;
  box-sizing: border-box;
  max-width: 640px;
  margin: 0 auto;
  line-height: 1.55;
}
.comment.reply {
  background: #ededed;
}
.comment:not(:last-of-type):after {
  display: block;
  content: "";
  width: 50%;
  height: 1px;
  margin: 0 auto;
  background: var(--dark-bg);
}
.comment .info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.comment .author {
  font-weight: bold;
}