/*
  Description: project's variables
*/
:root {
  --light-color: #4bc4b1;
  --main-color: #5f5f5f;
  --secondary-color: #1a4860;
  --site-color: #1B4751;
  --high-contrast-color: #AFDED6;
  --dark-bg: #173d51;
  --break-small: 415px;
  --break-medium: 768px;
  --break-large: 1024px;
  --break-wide: 1280px;
}

.contrast {
  background: #333;
  color: #eee;
}
.contrast article a, .contrast .wrapper a, .contrast a {
  color: var(--high-contrast-color);
}
.contrast article a:after, .contrast .wrapper a:after, .contrast a:after {
  background: var(--high-contrast-color);
}
.contrast article a:focus, .contrast article a:active, .contrast article a:hover, .contrast .wrapper a:focus, .contrast .wrapper a:active, .contrast .wrapper a:hover, .contrast a:focus, .contrast a:active, .contrast a:hover {
  color: #eee;
}
.contrast article a:focus:after, .contrast article a:active:after, .contrast article a:hover:after, .contrast .wrapper a:focus:after, .contrast .wrapper a:active:after, .contrast .wrapper a:hover:after, .contrast a:focus:after, .contrast a:active:after, .contrast a:hover:after {
  background: #eee;
}
.contrast article a:focus-visible, .contrast .wrapper a:focus-visible, .contrast a:focus-visible {
  outline: 2px solid var(--light-color);
}
.contrast article .highlight {
  background: #ccc;
}
.contrast header a, .contrast header button {
  color: var(--high-contrast-color);
}
.contrast header a:focus, .contrast header a:active, .contrast header a:hover, .contrast header button:focus, .contrast header button:active, .contrast header button:hover {
  color: #eee;
}
.contrast header a:focus:after, .contrast header a:active:after, .contrast header a:hover:after, .contrast header button:focus:after, .contrast header button:active:after, .contrast header button:hover:after {
  background: #eee;
}
.contrast h2.center {
  color: #eee;
}
.contrast code {
  color: var(--high-contrast-color);
}
.contrast pre code {
  color: #000;
}
.contrast .intro,
.contrast .project,
.contrast .about,
.contrast .contact,
.contrast .form_comment,
.contrast .experience,
.contrast footer {
  background: none;
  border-top: 1px solid #eee;
}
.contrast header,
.contrast .portfolio,
.contrast .bg_shadow {
  background: none;
}
.contrast .intro a, .contrast [download], .contrast [role=button] {
  background: none;
  border: 1px solid var(--high-contrast-color);
  transition: color 0.2s, border-color 0.2s;
}
.contrast .intro a:focus, .contrast .intro a:active, .contrast .intro a:hover, .contrast [download]:focus, .contrast [download]:active, .contrast [download]:hover, .contrast [role=button]:focus, .contrast [role=button]:active, .contrast [role=button]:hover {
  border-color: #eee;
}
.contrast .intro a:focus-visible, .contrast [download]:focus-visible, .contrast [role=button]:focus-visible {
  outline: 2px solid var(--light-color);
}
.contrast .intro {
  background: none;
}
.contrast .project_list .detail {
  background: none;
  color: inherit;
}
.contrast .project_list .type {
  color: inherit;
}
.contrast .project_list p {
  color: #eee;
}
.contrast .list_xp li.highlight {
  background: none;
  border: 1px solid #eee;
}
.contrast .list_xp li.highlight .bottom {
  border-color: #eee;
}
.contrast .list_xp h3 {
  color: var(--high-contrast-color);
}
.contrast .list_xp a {
  color: var(--light-color);
}
.contrast .list_xp a:focus, .contrast .list_xp a:active, .contrast .list_xp a:hover {
  color: #fff;
}
.contrast .list_xp a:focus:after, .contrast .list_xp a:active:after, .contrast .list_xp a:hover:after {
  background: #fff;
}
.contrast .list_xp a:focus-visible {
  outline: 2px solid var(--light-color);
}
.contrast .post_list > div > a:focus-visible::before {
  outline: 2px solid var(--light-color);
}
.contrast .social a:focus, .contrast .social a:active, .contrast .social a:hover {
  box-shadow: 0 0 10px #eee;
}
.contrast .social a:focus-visible {
  outline: 2px solid var(--light-color);
}
.contrast .about img {
  display: none;
}
.contrast .about p {
  max-width: 100%;
}
.contrast .contact a {
  color: var(--high-contrast-color);
}