/*
  Description: project's variables
*/
:root {
  --light-color: #4bc4b1;
  --main-color: #5f5f5f;
  --secondary-color: #1a4860;
  --site-color: #1B4751;
  --high-contrast-color: #AFDED6;
  --dark-bg: #173d51;
  --break-small: 415px;
  --break-medium: 768px;
  --break-large: 1024px;
  --break-wide: 1280px;
}

/* header with picture */
main > div {
  padding: 60px 20px 80px;
}
main > div.banner {
  padding: 0;
}

.banner {
  position: relative;
  background: #161b1f;
  max-height: 399px;
}
@media (max-width: 63.99em) {
  .banner {
    max-height: none;
  }
}

.visual {
  display: block;
  width: 100%;
  height: auto;
}
@media (max-width: 25.9275em) {
  .visual {
    width: 200%;
    margin-left: -50%;
    height: auto;
  }
}

.bg_shadow {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  z-index: 50;
  background: rgba(26, 72, 96, 0.9);
}
@media (max-width: 63.99em) {
  .bg_shadow {
    display: none;
  }
}

.intro {
  position: absolute;
  right: 0;
  top: 0;
  padding: 70px 0;
  z-index: 98;
  color: #fff;
  font-size: 1.6rem;
  text-align: center;
  hyphens: none;
}
@media (max-width: 63.99em) {
  .intro {
    position: relative;
    background: rgba(26, 72, 96, 0.9);
    padding: 50px 20px;
  }
}
.intro h1 {
  margin: 1rem 0 0.4rem;
  font-family: atk_bold, arial, sans-serif;
  font-size: 3rem;
  text-transform: uppercase;
}
.intro h1 span {
  display: block;
  margin-bottom: 6rem;
  font-family: atk_reg, arial, sans-serif;
  font-size: 1.6rem;
  text-transform: none;
}
.dyslexia .intro h1 {
  font-family: od_bold, arial, sans-serif;
}
.dyslexia .intro h1 span {
  font-family: od_reg, arial, sans-serif;
}
.intro ul {
  list-style: none;
}
.intro li {
  display: inline-block;
  vertical-align: top;
}
.intro li:not(:last-child) {
  margin-right: 8px;
}
@media (max-width: 63.99em) {
  .intro li {
    margin: 0 0 16px 0;
  }
}
.intro a {
  display: inline-block;
  vertical-align: top;
}
.intro a:focus-visible {
  outline: 2px solid var(--light-color);
}

/*liste vignettes*/
.project_list {
  -webkit-perspective: 200px;
  perspective: 200px;
  -webkit-perspective-origin: 50% 0;
  perspective-origin: 50% 0;
  text-align: center;
}
@media (min-width: 80em) {
  .project_list {
    text-align: left;
  }
}
.project_list .item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 8px 16px 0;
  border-radius: 4px;
  box-shadow: 0 0 5px #bababa;
  opacity: 0;
  transition: box-shadow 0.2s, transform 0.2s;
}
.ie .project_list .item {
  opacity: 1;
}
@media (max-width: 25.9275em) {
  .project_list .item {
    margin: 0 0 16px 0;
  }
}
.project_list .container {
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
.project_list .item:nth-child(1) {
  transform: translateZ(0);
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
  backface-visibility: hidden;
  animation: show_project 1s 1;
  animation-fill-mode: forwards;
  animation-delay: 0.25s;
}
@keyframes show_project {
  0% {
    opacity: 0;
    transform: translateZ(-20px);
  }
  100% {
    opacity: 1;
    transform: translateZ(0);
  }
}
