.project_list img {
  display: block;
  opacity: 1;
  transition: opacity 0.2s;
}
@media (max-width: 25.9275em) {
  .project_list img {
    max-width: 100%;
  }
}
.project_list a {
  display: block;
  position: relative;
  overflow: hidden;
  max-width: 324px;
  text-align: left;
}
.project_list a:focus span:after, .project_list a:active span:after, .project_list a:hover span:after {
  width: 100%;
  background: var(--main-color);
}
.project_list a:focus img, .project_list a:active img, .project_list a:hover img {
  opacity: 0.6;
}
.project_list .detail {
  padding: 8px 16px;
  background: #fff;
  font-size: 1.6rem;
  box-sizing: border-box;
  max-width: 324px;
  text-transform: uppercase;
  color: var(--site-color);
}
.project_list h3 {
  font-family: atk_bold, arial, sans-serif;
}
.dyslexia .project_list h3 {
  margin-bottom: 4px;
  font-family: od_bold, arial, sans-serif;
}
.project_list h3 span {
  display: inline-block;
  vertical-align: top;
  padding: 8px 16px 4px;
}
.project_list h3 span:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 0;
  background: #173d51;
  transition: width 0.2s;
}
.project_list p {
  padding: 0 16px;
  color: #5f5f5f;
  text-align: left;
  line-height: 2rem;
}

/* about */
.about {
  position: relative;
  background: #ededed;
}
.about img {
  display: inline-block;
  vertical-align: top;
  opacity: 0;
}
@media (max-width: 63.99em) {
  .about img {
    position: relative;
    left: 50%;
    margin: 0 0 30px -110px;
  }
}
@media (prefers-reduced-motion: reduce) {
  .about img.display {
    opacity: 1;
  }
}
.about p {
  display: inline-block;
  vertical-align: top;
  max-width: 566px;
  margin-left: 32px;
  font-size: 1.6rem;
}
@media (max-width: 63.99em) {
  .about p {
    max-width: 100%;
    margin: 0;
  }
}
.about a {
  color: #173d51;
}
.about a:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 100%;
  background: #173d51;
  transition: width 0.2s;
}
.about a:focus, .about a:active, .about a:hover {
  color: var(--main-color);
}
.about a:focus:after, .about a:active:after, .about a:hover:after {
  width: 0;
  background: var(--main-color);
}

/* parcours */
.list_xp ul {
  margin-left: 20px;
}
.list_xp > div {
  opacity: 0;
  margin: 0;
  padding: 30px 0;
  border-bottom: 1px solid #ededed;
  font-size: 1.6rem;
  list-style-type: none;
}
.list_xp > div.highlight {
  padding: 30px 0 0;
  background: var(--site-color);
  box-shadow: 0 0 15px #cdcdce;
  border-radius: 4px;
  border: 0;
}
@media (prefers-reduced-motion: reduce) {
  .list_xp > div.display {
    opacity: 1;
  }
}
.list_xp h3 {
  color: var(--site-color);
  margin-bottom: 12px;
}
.list_xp a {
  color: var(--site-color);
  margin-top: 12px;
}
.list_xp a:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 0;
  background: var(--site-color);
  transition: width 0.2s, background-color 0.2s;
}
.list_xp a:focus:after, .list_xp a:active:after, .list_xp a:hover:after {
  width: 100%;
}
.list_xp p a {
  display: inline-block;
  margin-right: 12px;
  text-transform: uppercase;
  font-family: atk_bold, arial, sans-serif;
}

.highlight ul {
  margin-left: 30px;
}
.highlight h3 {
  padding: 0 20px 12px;
  color: #fafafa;
  font-size: 2.4rem;
  line-height: 1.25;
}
.highlight li, .highlight p {
  padding: 0;
  color: #e9e9e9;
}
.highlight .bottom {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  border-top: 1px solid #38637a;
  padding: 15px 30px;
  margin-top: 20px;
}
.highlight .bottom p {
  padding: 0;
  display: inline-block;
  vertical-align: top;
}
.highlight .bottom a {
  color: var(--light-color);
  margin-top: 0;
}
.highlight .bottom a:focus, .highlight .bottom a:active, .highlight .bottom a:hover {
  color: #fff;
}
.highlight .bottom a:focus:after, .highlight .bottom a:active:after, .highlight .bottom a:hover:after {
  background: #fff;
}
.highlight .bottom a:focus-visible {
  outline: 2px solid var(--light-color);
}

.buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
  margin-top: 4rem;
}
.buttons p {
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 0;
  font-family: atk_bold, arial, sans-serif;
  font-size: 2rem;
}
.buttons > a {
  line-height: 1.25;
  border: 2px solid var(--secondary-color);
}
.buttons small {
  display: block;
  font-size: 1.2rem;
  line-height: 1.25;
}
@media (max-width: 63.99em) {
  .buttons {
    display: block;
  }
  .buttons p {
    display: block;
    margin-bottom: 25px;
    text-align: center;
  }
  .buttons > a {
    display: block;
    margin-bottom: 40px;
  }
  .buttons ul {
    text-align: center;
  }
}

.social li {
  display: inline-block;
  vertical-align: top;
}
.social li:not(:first-child) {
  margin-left: 16px;
}
.social a {
  padding: 0.3rem 0.5rem 0;
  border-radius: 100%;
  background: #ededed;
  border: 1px solid var(--site-color);
  transition: box-shadow 0.3s;
}
.social a:focus, .social a:active, .social a:hover {
  box-shadow: 0 0 5px var(--site-color);
}
.social svg {
  fill: var(--secondary-color);
}

.contact {
  background: var(--secondary-color);
}
.contact h2, .contact a {
  color: var(--light-color);
}
.contact p {
  margin: 0 auto;
  text-align: center;
  font-size: 1.6rem;
  line-height: 2.6rem;
  color: #fff;
}
@media (max-width: 63.99em) {
  .contact p {
    color: #fff;
  }
}
.contact p:after {
  display: block;
  margin: 20px auto 0;
  content: "";
  border-bottom: 1px solid #c5c5c5;
  width: 50px;
}
.contact a {
  transition: color 0.2s, background-color 0.2s;
}
.contact a:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 0;
  background: transparent;
  transition: width 0.2s, background-color 0.2s;
}
.contact a:focus, .contact a:active, .contact a:hover {
  color: #fff;
}
.contact a:focus:after, .contact a:active:after, .contact a:hover:after {
  width: 100%;
  background: #fff;
}
.contact a:focus-visible {
  outline: 2px solid var(--light-color);
}