/* global styles */
html {
  font-size: 62.5%;
  font-size: 0.625rem;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

@media (max-width: 25.9275em) {
  html, body {
    width: 100%;
    overflow-x: hidden;
  }
}

body {
  margin: 0;
  font-size: 100%;
  font-family: atk_reg, arial, sans-serif;
  font-weight: normal;
  color: var(--main-color);
  -webkit-font-smoothing: antialiased;
  width: 100%;
  background: #fafafa;
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  height: 100%;
  -webkit-flex-direction: column;
  flex-direction: column;
  line-height: 2.2rem;
}

.skip_links {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.skip_links a:focus {
  width: auto;
  height: auto;
  clip: auto;
  overflow: visible;
  z-index: 99;
  color: #fff;
  font-size: 1.6rem;
}
.skip_links a:focus-visible {
  outline: 2px solid var(--light-color);
}

.page-wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  flex-direction: column;
  display: flex;
  min-height: 100vh;
}

.wrapper {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
}

a {
  display: inline-block;
  text-decoration: none;
  color: var(--site-color);
  transition: 0.2s;
}
a.link_full_block:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: url("../img/global/link.png") 0 0 repeat;
}
a:focus-visible {
  outline: 2px solid var(--site-color);
}

pre {
  font-size: 1.6rem;
}

code {
  font-family: monospace;
}

blockquote, q {
  quotes: "“" "”" "‘" "’";
  font-size: 1.8rem;
  line-height: 1.55;
  margin-bottom: 2rem;
}

:lang(fr) > blockquote,
:lang(fr) > q {
  quotes: "« " " »" "“" "”" "‘" "’";
}

:lang(en) > blockquote,
:lang(en) > q {
  quotes: "“" "”" "‘" "’";
}

blockquote:before,
q:before {
  content: open-quote;
}

blockquote:after,
q:after {
  content: close-quote;
}

button:not(.display_menu) {
  -webkit-appearance: none;
  border: 0;
  background: transparent;
  cursor: pointer;
  transition: color 0.2s, background-color 0.2s;
}

strong, b {
  font-weight: bold;
}

em, i {
  font-style: italic;
}

h1, h2, h3, h4 {
  line-height: 1.5;
}

h1 {
  font-size: 3.5rem;
  margin-bottom: 3rem;
}

h2 {
  font-size: 2.5rem;
  margin: 1.6rem 0;
}
h2.center {
  margin: 0;
  padding: 2rem 0 8rem;
  color: var(--secondary-color);
