  font-size: 5rem;
}

h3 {
  font-size: 2.2rem;
  font-family: atk_bold, arial, sans-serif;
  margin-bottom: 1rem;
}

h4 {
  font-size: 1.5rem;
}

p {
  font-family: atk_reg, helvetica, sans-serif;
}

main {
  -webkit-flex: 1;
  flex: 1 0 auto;
}
main p, main li {
  font-size: 1.6rem;
}
main p {
  margin-bottom: 2rem;
}
main li {
  list-style: disc outside;
  margin: 0 0 0.5rem 2rem;
}

::-moz-selection {
  background: var(--site-color);
  color: #fff;
}

::selection {
  background: var(--site-color);
  color: #fff;
}

::-webkit-input-placeholder { /* WebKit browsers */
  color: #131A1C;
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color: #131A1C;
  opacity: 1;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #131A1C;
  opacity: 1;
}

:-ms-input-placeholder { /* Internet Explorer 10+ */
  color: #131A1C;
}

.display {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-animation: display_item 1s 1;
  animation: display_item 1s 1;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-delay: 1s;
  -moz-animation-delay: 1s;
  animation-delay: 1s;
  transform: translateY(-30px);
}
@keyframes display_item {
  0% {
    opacity: 0;
    transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    transform: translateY(5px);
  }
}

div, textarea {
  word-wrap: break-word;
  hyphens: auto;
}

@media (max-width: 63.99em) {
  .hidden_small {
    display: none;
  }
}

.center_content {
  position: relative;
  max-width: 1240px;
  margin: 0 auto;
}
@media (max-width: 63.99em) {
  .center_content {
    padding: 0 2rem;
  }
}

.center {
  text-align: center;
}

.intro a, [download],
[role=button], input[type=submit] {
  min-width: 13rem;
  box-sizing: border-box;
  padding: 1.8rem 2rem;
  background: var(--light-color);
  border-radius: 0.2rem;
  text-align: center;
  text-transform: uppercase;
  color: var(--site-color);
  font-family: atk_bold, arial, sans-serif;
  font-size: 1.6rem;
  transition: 0.2s;
}
.intro a:focus, .intro a:active, .intro a:hover, [download]:focus, [download]:active, [download]:hover,
[role=button]:focus,
[role=button]:active,
[role=button]:hover, input[type=submit]:focus, input[type=submit]:active, input[type=submit]:hover {
  background: var(--site-color);
  color: var(--light-color);
  box-shadow: 0 0 1px 2px var(--light-color);
}

[download]:focus, [download]:active, [download]:hover {
  box-shadow: none;
}

.dyslexia body, .dyslexia a, .dyslexia button, .dyslexia p, .dyslexia li, .dyslexia .btn, .dyslexia h1, .dyslexia h2, .dyslexia h3, .dyslexia h4, .dyslexia header a:not(.logo) {
  font-family: od_reg, arial, sans-serif;
  line-height: 1.5;
}

.top_page {
  position: fixed;
  bottom: 20px;
  right: 20px;
  opacity: 0;
  transition: opacity 0.2s;
}
.top_page.visible {
  opacity: 1;
}
@media (min-width: 80em) {
  .top_page {
    right: 100px;
    bottom: 130px;
  }
}
.top_page:focus:after, .top_page:active:after, .top_page:hover:after {
  width: 0;
}
.top_page:focus-visible {
  box-shadow: 0 0 1px 2px var(--light-color), 0 0 1px 4px var(--site-color);
}

/* end global styles */
.not_found {
  display: block;
  width: 100%;
  height: auto;
}

