.main_menu {
  position: relative;
}

.main_menu li {
  display: block;
  margin-bottom: 15px;
}

.display_menu {
  position: relative;
  width: 40px;
  height: 40px;
  border: 0;
  background: transparent;
  transition: 0.2s;
}

.display_menu .caret {
  display: block;
  position: absolute;
  left: 50%;
  top: 20px;
  margin: 0 0 0 -15px;
  cursor: pointer;
  height: 3px;
  width: 30px;
  background: var(--light-color);
  transition: 0.2s;
}

.display_menu .caret:before, .display_menu .caret:after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  margin-left: -15px;
  cursor: pointer;
  height: 3px;
  width: 30px;
  background: var(--light-color);
  transition: 0.3s;
}

.display_menu .caret:before {
  top: 8px;
}

.display_menu .caret:after {
  bottom: 8px;
}

.display_menu.active span {
  background-color: transparent;
}

.display_menu.active span:before, .display_menu.active span:after {
  top: 0;
}

.display_menu.active span:before {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.display_menu.active span:after {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

