  height: 33px;
}

footer {
  background: var(--dark-bg);
  padding: 16px 20px;
  color: #a1bfcf;
  font-size: 1.6rem;
}

@media (min-width: 64em) {
  footer {
    padding: 26px 20px 56px;
  }
}
@media (min-width: 64em) and (max-width: 79.99em) {
  footer {
    padding: 26px 20px 56px;
  }
}
footer .wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
}

@media (max-width: 63.99em) {
  footer .wrapper {
    display: block;
  }
}
footer li {
  display: inline-block;
  vertical-align: top;
}

@media (max-width: 63.99em) {
  footer li {
    margin: 3rem 0 0;
  }
}
footer a {
  color: #fff;
  font-size: 1.6rem;
  line-height: 1.6rem;
  transition: color 0.2s, background-color 0.2s;
}

footer a:after {
  content: "";
  display: block;
  margin: 3px auto 0;
  height: 1px;
  width: 0;
  background: transparent;
  transition: width 0.2s, background-color 0.2s;
}

footer a:focus, footer a:active, footer a:hover {
  color: var(--light-color);
}

footer a:focus:after, footer a:active:after, footer a:hover:after {
  width: 100%;
  background: var(--light-color);
}

footer a:focus-visible {
  outline: 2px solid var(--light-color);
}

footer li:not(:first-child) {
  margin-left: 20px;
}

footer .top_page {
  background: var(--dark-bg);
  padding: 0.5rem 0.8rem 0.2rem;
}

footer .top {
  fill: var(--light-color);
}

#cookie-consent-bar {
  background: rgba(231, 231, 231, 0.9);
  display: block;
  left: 0;
  padding: 1rem 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 99;
  font-size: 1.6rem;
}
.contrast #cookie-consent-bar {
  color: var(--main-color);
  background: #eee;
}
#cookie-consent-bar p {
  position: relative;
  max-width: 1240px;
  margin: 0 auto 1rem;
  line-height: 1.55;
}
@media (max-width: 63.99em) {
  #cookie-consent-bar p {
    padding: 0 2rem;
  }
}
#cookie-consent-bar button {
  margin-right: 2rem;
}
#cookie-consent-bar .sb-enable, #cookie-consent-bar .sb-disable {
  box-sizing: border-box;
  padding: 0.5rem 1rem;
  background: var(--light-color);
  border-radius: 0.2rem;
  text-align: center;
  color: var(--site-color);
  font-family: atk_bold, arial, sans-serif;
  font-size: 1.6rem;
  transition: 0.2s;
}
#cookie-consent-bar .sb-disable {
  background: var(--site-color);
  color: var(--light-color);
}
#cookie-consent-bar .sb-policy {
  display: inline-block;
}
.contrast #cookie-consent-bar .sb-policy {
  color: var(--main-color);
}
#cookie-consent-bar .sb-policy:after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 1px;
  width: 100%;
  background: var(--main-color);
  transition: width 0.2s;
}
#cookie-consent-bar .sb-policy:focus, #cookie-consent-bar .sb-policy:active, #cookie-consent-bar .sb-policy:hover {
  color: var(--main-color);
}
#cookie-consent-bar .sb-policy:focus:after, #cookie-consent-bar .sb-policy:active:after, #cookie-consent-bar .sb-policy:hover:after {
  width: 0;
  background: var(--main-color);
}
#cookie-consent-bar a:focus-visible, #cookie-consent-bar button:focus-visible {
  outline: 2px solid var(--site-color);
}